:root {
  --blue-color: #00acdc;
}

@font-face {
  font-family: Roboto;
  font-weight: 400;
  src: url("Roboto-Regular.ca197847.ttf");
}

@font-face {
  font-family: Roboto;
  font-weight: 500;
  src: url("Roboto-Medium.91322885.ttf");
}

body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: background-color .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link[data-qa="hover"]:hover, .nav__link.is-active {
  color: var(--blue-color);
}

.nav__link.is-active:hover:after {
  background-color: var(--blue-color);
}

img {
  vertical-align: bottom;
  margin: 10px 0;
}



